body {
  margin: 0;
  padding: 0;
  font-family: recharge;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: recharge;
	src: url("https://sondrehusevold.github.io/CyberpunkRPG/assets/recharge.ttf");
  }

*::-moz-focus-inner {
	border: 0;
  }
.Login_Login__1-lbI {
	margin-top: 10%;
	display:grid;
	grid-template-columns: 1fr minmax(210px, 325px) 1fr;
	grid-template-rows: 1fr 1fr;
	grid-row-gap: 20px;
	color: red;
	font-family: recharge;
}

.Login_Login__1-lbI input {
	background-color: #680001FF !important;
	font-family: recharge;
	padding: 3px;
	color: white;
	-webkit-transition: border 0.2s;
	transition: border 0.2s;
	border: 1px solid transparent;
	border-radius: 2px;
	cursor: text;
	outline: 0;
}

.Login_Login__1-lbI input:hover {
	-webkit-transition: border 0.2s;
	transition: border 0.2s;
	border: 1px solid red;
	border-radius: 2px;
}

.Login_Login__1-lbI input:focus {
	border: 1px solid red;
	border-radius: 2px;
}

.Login_LoginColumn__1Itek {
	grid-column: 2;
}

.Login_LoginUsername__3GcBD {
	grid-column: 2;
	grid-row: 1;
	display: flex;
	flex-direction: column;
	text-align: center;
	justify-content: center;
}

.Login_LoginPassword__3EZJc {
	grid-column: 2;
	grid-row: 2;
	display: flex;
	flex-direction: column;
	text-align: center;
	justify-content: center;
}

.Login_LoginAttempts__Je2ti {
	grid-column: 2 / span 2;
	margin-left: -5%;
	grid-row: 3;
}

.Login_LoginButton__1PCXK {
	grid-column: 2;
	grid-row: 4;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	align-content: center;

	font-size: 16px;
	font-family: recharge;
	margin-left: 30%;
	margin-right: 30%;
	background-color: transparent;
	border: 1px solid transparent;
	padding: 7px;
	border-radius: 5px;
	-webkit-transition: border color 0.1s;
	transition: border color 0.1s;
	color: red;
}

.Login_LoginButton__1PCXK:hover {
	border: 1px solid red;
	border-radius: 5px;
	-webkit-transition: border 0.2s;
	transition: border 0.2s;
}

.Login_LoginButton__1PCXK:active {
	-webkit-transition: border 0s;
	transition: border 0s;
	border-color: darkred;
	color: darkred;
}
.Mainframe_Zetatech__P0DEn {
	color: red;
}

.Mainframe_Background__KrUYa {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
}
.Terminal_Terminal__3NPlf {
	display: flex;
	margin-left: 15%;
	margin-right: 15%;
	margin-top: 5%;
	height: 80vh;
	outline: 0;
}

.Terminal_Commands__N6ASV {
	font-size: 25px;
	font-family: 'recharge';
	overflow: none;
	width: 100%;
	outline: 0;
}

.Terminal_CommandLine__3L9xf {
	display: flex;
	flex-direction: row;
	flex: 1 1;
	width: 100%;
	outline: 0;
}

.Terminal_InvisibleInput__2ugGz {
	font-size: 25px;
	font-family: 'recharge';
	color: red;
	background-color: transparent;
	border: 0;
	width: 1fr;
	outline: 0;
}
